






import Vue from "vue";
import axios from "axios";

export default Vue.extend({
    components: {
    },
    props: {
        markdownPath: {
            type: String,
            required: true,
        },
    },
    data() {
        return {
            markdown: String,
        };
    },
    mounted() {
        this.loadMarkdown();
    },
    methods: {
        async loadMarkdown() {
            this.markdown = await axios.get(this.markdownPath);
        },
    },
});
